import React, { Component, PropTypes } from 'react';
import d3 from 'd3';
import '../../css/forceDirectedGraph.css';

export default class ForceDirectedGraph extends React.Component {
    constructor() {
        super(...arguments);
        this.width  = 500;	//SVG
        this.height = 400;	//SVG
    }
    componentDidMount() {

        let width  = this.width;	//SVG
        let height = this.height;	//SVG

        let svg = d3.select(".forceLayout")			//<body>
            .append("svg")			//<body><svg>
            .attr("width", width)	//<svg>
            .attr("height", height);//<svg>

        //1.
        let nodes = [ { name: "0" ,text:""   },
            { name: "1" ,text:""},
            { name: "2" ,text:""   },
            { name: "3" ,text:""  },
            { name: "4" ,text:""  },
            { name: "5" ,text:""  },
            { name: "6" ,text:""  } ];

        let edges = [  { source : 0  , target: 1 } ,
            { source : 0  , target: 2 } ,
            { source : 0  , target: 3 } ,
            { source : 1  , target: 4 } ,
            { source : 1  , target: 5 } ,
            { source : 2  , target: 6 },
        ];

        //2.
        let force = d3.layout.force()
            .nodes(nodes)	//
            .links(edges)	//
            .size([width,height])	//
            .linkDistance(90)	//
            .charge(-400);	//

        force.start();	//

        console.log(nodes);	//
        console.log(edges);

        //3.
        let color = d3.scale.category20();

        //
        let lines = svg.selectAll(".forceLine")
            .data(edges)
            .enter()
            .append("line")
            .attr("class","forceLine");

        //
        let circles = svg.selectAll(".forceCircle")
            .data(nodes)
            .enter()
            .append("circle")
            .attr("class","forceCircle")
            .attr("r",25)
            .style("fill",function(d,i){
                return color(i);
            })
            .call(force.drag);

        //
        let texts = svg.selectAll(".forceText")
            .data(nodes)
            .enter()
            .append("text")
            .attr("class","forceText")
            .attr("x",function(d){ return d.x; })
            .attr("y",function(d){ return d.y; })
            .attr("dy", ".3em")
            .text(function(d){ return d.text; });

        //tick
        force.on("tick", function(){

            //
            lines.attr("x1",function(d){ return d.source.x; });
            lines.attr("y1",function(d){ return d.source.y; });
            lines.attr("x2",function(d){ return d.target.x; });
            lines.attr("y2",function(d){ return d.target.y; });

            //
            circles.attr("cx",function(d){ return d.x; });
            circles.attr("cy",function(d){ return d.y; });

            //
            texts.attr("x",function(d){ return d.x; });
            texts.attr("y",function(d){ return d.y; });

        });


        //
        force.on("start", function(){
            console.log("");
        });

        //
        force.on("end", function(){
            console.log("");
        });
    }
    render() {
        return (
            <div className="outer">
                <div className="main" key="a">
                    <div className="forceLayout"  key="b"></div>
                </div>
            </div>);
    }

}





